import React from "react";
import cookie from "js-cookie";
import { Box, Container, Card, Grid, CardContent } from "@material-ui/core";
import { useDispatch } from "react-redux";
import { AuthTokenKey } from "../../utills/constants";
import { RouteURLs } from "../../routes/routes";
const AuthLayout: React.FC<any> = ({
  component: Component,
}: any): JSX.Element => {
  const dispatch = useDispatch();
  const isLoggedIn = cookie.get(AuthTokenKey);
  // localStorage.getItem(AuthTokenKey);
  if (isLoggedIn) {
    dispatch({
      path: RouteURLs.DASHBOARD,
    });
  }
  return (
    <>
      <Box>
        <Container>
          <Grid
            container
            direction="row"
            justify="center"
            style={{
              height: "90vh",
            }}
            alignItems="center"
          >
            <Grid sm={6} md={5} xs={12} item>
              <Card className="auth-card">
                <CardContent>
                  <Component />
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
};

export default AuthLayout;
